async function realDBQueueItems() {
  const userData = getAuth().onAuthStateChanged(user => {
    if (user) {
      //    ,       

      const userMail = user.email;
      const colRealRef = collection(db, userMail);

      onSnapshot(colRealRef, snapshot => {
        let user = [];
        snapshot.docs.forEach(doc => {
          user.push({ ...doc.data() });
        });
        console.log(user);
      });
    }
  });
}

